<template>
  <div class="vue-image-crop-upload" v-show="value">
    <div class="vicp-wrap">
      <div class="vicp-close" @click="off">
        <i class="vicp-icon4"></i>
      </div>

      <div class="vicp-step1" v-show="step == 1">
        <div class="vicp-drop-area" @dragleave="preventDefault" @dragover="preventDefault" @dragenter="preventDefault"
             @click="handleClick" @drop="handleChange">
          <i class="vicp-icon1" v-show="loading != 1">
            <i class="vicp-icon1-arrow"></i>
            <i class="vicp-icon1-body"></i>
            <i class="vicp-icon1-bottom"></i>
          </i>
          <span class="vicp-hint" v-show="loading !== 1">{{ lang.hint }}</span>
          <span class="vicp-no-supported-hint" v-show="!isSupported">{{ lang.noSupported }}</span>
          <input type="file" v-show="false" v-if="step == 1" @change="handleChange" ref="fileinput">
        </div>
        <div class="vicp-error" v-show="hasError">
          <i class="vicp-icon2"></i> {{ errorMsg }}
        </div>
        <div class="vicp-operate">
          <a @click="off" @mousedown="ripple">{{ lang.btn.off }}</a>
        </div>
      </div>

      <div class="vicp-step2" v-if="step == 2">
        <div class="vicp-crop">
          <div class="vicp-crop-left" v-show="true">
            <div class="vicp-img-container">
              <img :src="sourceImgUrl" :style="sourceImgStyle" class="vicp-img" draggable="false"
                   @drag="preventDefault"
                   @dragstart="preventDefault"
                   @dragend="preventDefault"
                   @dragleave="preventDefault"
                   @dragover="preventDefault"
                   @dragenter="preventDefault"
                   @drop="preventDefault"
                   @touchstart="imgStartMove"
                   @touchmove="imgMove"
                   @touchend="createImg"
                   @touchcancel="createImg"
                   @mousedown="imgStartMove"
                   @mousemove="imgMove"
                   @mouseup="createImg"
                   @mouseout="createImg"
                   ref="img">
              <div class="vicp-img-shade vicp-img-shade-1" :style="sourceImgShadeStyle"></div>
              <div class="vicp-img-shade vicp-img-shade-2" :style="sourceImgShadeStyle"></div>
            </div>

            <div class="vicp-range">
              <input type="range" :value="scale.range" step="1" min="0" max="100" @input="zoomChange">
              <i @mousedown="startZoomSub" @mouseout="endZoomSub" @mouseup="endZoomSub" class="vicp-icon5"></i>
              <i @mousedown="startZoomAdd" @mouseout="endZoomAdd" @mouseup="endZoomAdd" class="vicp-icon6"></i>
            </div>

            <div class="vicp-rotate" v-if="!noRotate">
              <i @click="rotateImg"></i>
            </div>
          </div>
          <div class="vicp-crop-right" v-show="true">
            <div class="vicp-preview">
              <div class="vicp-preview-item" v-if="!noSquare">
                <img :src="createImgUrl" :style="previewStyle">
                <span>{{ lang.preview }}</span>
              </div>
              <div class="vicp-preview-item vicp-preview-item-circle" v-if="!noCircle">
                <img :src="createImgUrl" :style="previewStyle">
                <span>{{ lang.preview }}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="vicp-operate">
          <a @click="setStep(1)" @mousedown="ripple">{{ lang.btn.back }}</a>
          <a class="vicp-operate-btn" @click="prepareUpload" @mousedown="ripple">{{ lang.btn.save }}</a>
        </div>
      </div>

      <div class="vicp-step3" v-if="step == 3">
        <div class="vicp-upload">
          <span class="vicp-loading" v-show="loading === 1">{{ lang.loading }}</span>
          <div class="vicp-progress-wrap">
            <span class="vicp-progress" v-show="loading === 1" :style="progressStyle"></span>
          </div>
          <div class="vicp-error" v-show="hasError">
            <i class="vicp-icon2"></i> {{ errorMsg }}
          </div>
          <div class="vicp-success" v-show="loading === 2">
            <i class="vicp-icon3"></i> {{ lang.success }}
          </div>
        </div>
        <div class="vicp-operate">
          <a @click="setStep(2)" @mousedown="ripple">{{ lang.btn.back }}</a>
          <a @click="off" @mousedown="ripple">{{ lang.btn.close }}</a>
        </div>
      </div>
      <canvas v-show="false" :width="width" :height="height" ref="canvas"></canvas>
    </div>
  </div>
</template>

<script>
import language from '../../../assets/js/language.js'
import mimes from '../../../assets/js/mimes.js'
import data2blob from '../../../assets/js/data2blob.js'
import effectRipple from '../../../assets/js/effectRipple.js'

export default {
  name: 'UploadImg',

  props: {
    // name
    field: {
      type: String,
      'default': 'avatar'
    },
    // keyid
    ki: {
      'default': 0
    },
    // 
    value: {
      'default': true
    },
    // 
    url: {
      type: String,
      'default': ''
    },
    // 
    params: {
      type: Object,
      'default': null
    },
    // Add custom headers
    headers: {
      type: Object,
      'default': null
    },
    // 
    width: {
      type: Number,
      default: 200
    },
    // 
    height: {
      type: Number,
      default: 200
    },
    // 
    noRotate: {
      type: Boolean,
      default: true
    },
    // 
    noCircle: {
      type: Boolean,
      default: false
    },
    // 
    noSquare: {
      type: Boolean,
      default: false
    },
    // 
    maxSize: {
      type: Number,
      'default': 10240
    },
    // 
    langType: {
      type: String,
      'default': 'zh'
    },
    // 
    langExt: {
      type: Object,
      'default': null
    },
    // 
    imgFormat: {
      type: String,
      'default': 'png'
    },
    //  jpg
    imgBgc: {
      type: String,
      'default': '#fff'
    },
    // 
    withCredentials: {
      type: Boolean,
      'default': false
    }
  },
  data () {
    let that = this
    let {
      imgFormat,
      langType,
      langExt,
      width,
      height
    } = that
    let isSupported = true
    let allowImgFormat = ['jpg', 'png']
    let tempImgFormat = allowImgFormat.indexOf(imgFormat) === -1 ? 'jpg' : imgFormat
    let lang = language[langType] ? language[langType] : language['en']
    let mime = mimes[tempImgFormat]
    // 
    that.imgFormat = tempImgFormat

    if (langExt) {
      Object.assign(lang, langExt)
    }
    if (typeof FormData !== 'function') {
      isSupported = false
    }
    return {
      // mime
      mime,

      // 
      lang,

      // 
      isSupported,
      // 
      isSupportTouch: document.hasOwnProperty('ontouchstart'),

      // 
      step: 1, // 1 2 3

      // 
      loading: 0, // 0 1 2 3
      progress: 0,

      // 
      hasError: false,
      errorMsg: '',

      // 
      ratio: width / height,

      // 
      sourceImg: null,
      sourceImgUrl: '',
      createImgUrl: '',

      // 
      sourceImgMouseDown: {
        on: false,
        mX: 0, // 
        mY: 0,
        x: 0, // scale
        y: 0
      },

      // 
      previewContainer: {
        width: 100,
        height: 100
      },

      // 
      sourceImgContainer: { // sic
        width: 240,
        height: 184 // bug
      },

      // 
      scale: {
        zoomAddOn: false, // 
        zoomSubOn: false, // 
        range: 1, // 100

        x: 0,
        y: 0,
        width: 0,
        height: 0,
        maxWidth: 0,
        maxHeight: 0,
        minWidth: 0, // 
        minHeight: 0,
        naturalWidth: 0, // 
        naturalHeight: 0
      }
    }
  },
  computed: {
    // 
    progressStyle () {
      let {
        progress
      } = this
      return {
        width: progress + '%'
      }
    },
    // 
    sourceImgStyle () {
      let {
        scale,
        sourceImgMasking
      } = this
      let top = scale.y + sourceImgMasking.y + 'px'
      let left = scale.x + sourceImgMasking.x + 'px'
      return {
        top,
        left,
        width: scale.width + 'px',
        height: scale.height + 'px'//  Opera
      }
    },
    // 
    sourceImgMasking () {
      let {
        width,
        height,
        ratio,
        sourceImgContainer
      } = this
      let sic = sourceImgContainer
      let sicRatio = sic.width / sic.height // 
      let x = 0
      let y = 0
      let w = sic.width
      let h = sic.height
      let scale = 1
      if (ratio < sicRatio) {
        scale = sic.height / height
        w = sic.height * ratio
        x = (sic.width - w) / 2
      }
      if (ratio > sicRatio) {
        scale = sic.width / width
        h = sic.width / ratio
        y = (sic.height - h) / 2
      }
      return {
        scale, // 
        x,
        y,
        width: w,
        height: h
      }
    },
    // 
    sourceImgShadeStyle () {
      let {
        sourceImgMasking,
        sourceImgContainer
      } = this
      let sic = sourceImgContainer
      let sim = sourceImgMasking
      let w = sim.width === sic.width ? sim.width : (sic.width - sim.width) / 2
      let h = sim.height === sic.height ? sim.height : (sic.height - sim.height) / 2
      return {
        width: w + 'px',
        height: h + 'px'
      }
    },
    previewStyle () {
      let {
        // width,
        // height,
        ratio,
        previewContainer
      } = this
      let pc = previewContainer
      let w = pc.width
      let h = pc.height
      let pcRatio = w / h
      if (ratio < pcRatio) {
        w = pc.height * ratio
      }
      if (ratio > pcRatio) {
        h = pc.width / ratio
      }
      return {
        width: w + 'px',
        height: h + 'px'
      }
    }
  },
  watch: {
    value (newValue) {
      if (newValue && this.loading !== 1) {
        this.reset()
      }
    }
  },
  methods: {
    // 
    ripple (e) {
      effectRipple(e)
    },
    // 
    off () {
      setTimeout(() => {
        this.$emit('input', false)
        if (this.step === 3 && this.loading === 2) {
          this.setStep(1)
        }
      }, 200)
    },
    // 
    setStep (no) {
      // 
      setTimeout(() => {
        this.step = no
      }, 200)
    },

    /**
     * 
     */
    preventDefault (e) {
      e.preventDefault()
      return false
    },
    handleClick (e) {
      if (this.loading !== 1) {
        if (e.target !== this.$refs.fileinput) {
          e.preventDefault()
          if (document.activeElement !== this.$refs) {
            this.$refs.fileinput.click()
          }
        }
      }
    },
    handleChange (e) {
      e.preventDefault()
      if (this.loading !== 1) {
        let files = e.target.files || e.dataTransfer.files
        this.reset()
        if (this.checkFile(files[0])) {
          this.setSourceImg(files[0])
        }
      }
    },

    /**
     * 
     */
    checkFile (file) {
      let that = this
      let {
        lang,
        maxSize
      } = that
      // 
      if (file.type.indexOf('image') === -1) {
        that.hasError = true
        that.errorMsg = lang.error.onlyImg
        return false
      }

      // 
      if (file.size / 1024 > maxSize) {
        that.hasError = true
        that.errorMsg = lang.error.outOfSize + maxSize + 'kb'
        return false
      }
      return true
    },
    // 
    reset () {
      let that = this
      that.loading = 0
      that.hasError = false
      that.errorMsg = ''
      that.progress = 0
    },
    // 
    setSourceImg (file) {
      let that = this
      let fr = new FileReader()
      fr.onload = function (e) {
        that.sourceImgUrl = fr.result
        that.startCrop()
      }
      fr.readAsDataURL(file)
    },
    // 
    startCrop () {
      let that = this
      let {
        width,
        height,
        ratio,
        scale,
        sourceImgUrl,
        sourceImgMasking,
        lang
      } = that
      let sim = sourceImgMasking
      let img = new Image()
      img.src = sourceImgUrl
      img.onload = function () {
        let nWidth = img.naturalWidth
        let nHeight = img.naturalHeight
        let nRatio = nWidth / nHeight
        let w = sim.width
        let h = sim.height
        let x = 0
        let y = 0
        // 
        if (nWidth < width || nHeight < height) {
          that.hasError = true
          that.errorMsg = lang.error.lowestPx + width + '*' + height
          return false
        }
        if (ratio > nRatio) {
          h = w / nRatio
          y = (sim.height - h) / 2
        }
        if (ratio < nRatio) {
          w = h * nRatio
          x = (sim.width - w) / 2
        }
        scale.range = 0
        scale.x = x
        scale.y = y
        scale.width = w
        scale.height = h
        scale.minWidth = w
        scale.minHeight = h
        scale.maxWidth = nWidth * sim.scale
        scale.maxHeight = nHeight * sim.scale
        scale.naturalWidth = nWidth
        scale.naturalHeight = nHeight
        that.sourceImg = img
        that.createImg()
        that.setStep(2)
      }
    },
    // 
    imgStartMove (e) {
      e.preventDefault()
      // 
      if (this.isSupportTouch && !e.targetTouches) {
        return false
      }
      let et = e.targetTouches ? e.targetTouches[0] : e
      let {
        sourceImgMouseDown,
        scale
      } = this
      let simd = sourceImgMouseDown
      simd.mX = et.screenX
      simd.mY = et.screenY
      simd.x = scale.x
      simd.y = scale.y
      simd.on = true
    },
    // 
    imgMove (e) {
      e.preventDefault()
      // 
      if (this.isSupportTouch && !e.targetTouches) {
        return false
      }
      let et = e.targetTouches ? e.targetTouches[0] : e
      let {
        sourceImgMouseDown: {on, mX, mY, x, y},
        scale,
        sourceImgMasking
      } = this
      let sim = sourceImgMasking
      let nX = et.screenX
      let nY = et.screenY
      let dX = nX - mX
      let dY = nY - mY
      let rX = x + dX
      let rY = y + dY
      if (!on) return
      if (rX > 0) {
        rX = 0
      }
      if (rY > 0) {
        rY = 0
      }
      if (rX < sim.width - scale.width) {
        rX = sim.width - scale.width
      }
      if (rY < sim.height - scale.height) {
        rY = sim.height - scale.height
      }
      scale.x = rX
      scale.y = rY
    },
    // 
    rotateImg (e) {
      let {
        sourceImg,
        scale: {
          naturalWidth,
          naturalHeight
        }
      } = this
      let width = naturalHeight
      let height = naturalWidth
      let canvas = this.$refs.canvas
      let ctx = canvas.getContext('2d')
      canvas.width = width
      canvas.height = height
      ctx.clearRect(0, 0, width, height)

      ctx.fillStyle = 'rgba(0,0,0,0)'
      ctx.fillRect(0, 0, width, height)

      ctx.translate(width, 0)
      ctx.rotate(Math.PI * 90 / 180)

      ctx.drawImage(sourceImg, 0, 0, naturalWidth, naturalHeight)
      let imgUrl = canvas.toDataURL(mimes['png'])

      this.sourceImgUrl = imgUrl
      this.startCrop()
    },

    // 
    startZoomAdd (e) {
      let that = this
      let {
        scale
      } = that
      scale.zoomAddOn = true

      function zoom () {
        if (scale.zoomAddOn) {
          let range = scale.range >= 100 ? 100 : ++scale.range
          that.zoomImg(range)
          setTimeout(function () {
            zoom()
          }, 60)
        }
      }

      zoom()
    },
    // 
    endZoomAdd (e) {
      this.scale.zoomAddOn = false
    },
    // 
    startZoomSub (e) {
      let that = this
      let {
        scale
      } = that
      scale.zoomSubOn = true

      function zoom () {
        if (scale.zoomSubOn) {
          let range = scale.range <= 0 ? 0 : --scale.range
          that.zoomImg(range)
          setTimeout(function () {
            zoom()
          }, 60)
        }
      }

      zoom()
    },
    // 
    endZoomSub (e) {
      let {
        scale
      } = this
      scale.zoomSubOn = false
    },
    zoomChange (e) {
      this.zoomImg(e.target.value)
    },
    // 
    zoomImg (newRange) {
      let that = this
      let {
        sourceImgMasking,
        // sourceImgMouseDown,
        scale
      } = this
      let {
        maxWidth,
        maxHeight,
        minWidth,
        minHeight,
        width,
        height,
        x,
        y
        // range
      } = scale
      let sim = sourceImgMasking
      // 
      let sWidth = sim.width
      let sHeight = sim.height
      // 
      let nWidth = minWidth + (maxWidth - minWidth) * newRange / 100
      let nHeight = minHeight + (maxHeight - minHeight) * newRange / 100
      // 
      let nX = sWidth / 2 - (nWidth / width) * (sWidth / 2 - x)
      let nY = sHeight / 2 - (nHeight / height) * (sHeight / 2 - y)

      // 
      if (nX > 0) {
        nX = 0
      }
      if (nY > 0) {
        nY = 0
      }
      if (nX < sWidth - nWidth) {
        nX = sWidth - nWidth
      }
      if (nY < sHeight - nHeight) {
        nY = sHeight - nHeight
      }

      // 
      scale.x = nX
      scale.y = nY
      scale.width = nWidth
      scale.height = nHeight
      scale.range = newRange
      setTimeout(function () {
        if (scale.range === newRange) {
          that.createImg()
        }
      }, 300)
    },
    // 
    createImg (e) {
      let that = this
      let {
        imgFormat, imgBgc, mime, sourceImg,
        scale: {x, y, width, height},
        sourceImgMasking: {scale}
      } = that
      let canvas = that.$refs.canvas
      let ctx = canvas.getContext('2d')
      if (e) {
        // 
        that.sourceImgMouseDown.on = false
      }
      canvas.width = that.width
      canvas.height = that.height
      ctx.clearRect(0, 0, that.width, that.height)

      if (imgFormat === 'png') {
        ctx.fillStyle = 'rgba(0,0,0,0)'
      } else {
        // jpg 
        ctx.fillStyle = imgBgc
      }
      ctx.fillRect(0, 0, that.width, that.height)

      ctx.drawImage(sourceImg, x / scale, y / scale, width / scale, height / scale)
      that.createImgUrl = canvas.toDataURL(mime)
    },
    prepareUpload () {
      let {
        url,
        createImgUrl,
        field,
        ki
      } = this
      this.$emit('crop-success', createImgUrl, field, ki)
      if (typeof url === 'string' && url) {
        this.upload()
      } else {
        this.off()
      }
    },
    // 
    upload () {
      let that = this
      let {
        lang, imgFormat, mime, url, params, headers, field, ki, createImgUrl, withCredentials
      } = this
      let fmData = new FormData()
      fmData.append(field, data2blob(createImgUrl, mime), field + '.' + imgFormat)

      // 
      if (typeof params === 'object' && params) {
        Object.keys(params).forEach((k) => {
          fmData.append(k, params[k])
        })
      }

      // 
      const uploadProgress = function (event) {
        if (event.lengthComputable) {
          that.progress = 100 * Math.round(event.loaded) / event.total
        }
      }

      // 
      that.reset()
      that.loading = 1
      that.setStep(3)
      new Promise((resolve, reject) => {
        let client = new XMLHttpRequest()
        client.open('POST', url, true)
        client.withCredentials = withCredentials
        client.onreadystatechange = function () {
          if (this.readyState !== 4) {
            return
          }
          if (this.status === 200 || this.status === 201) {
            resolve(JSON.parse(this.responseText))
          } else {
            reject(this.status)
          }
        }
        client.upload.addEventListener('progress', uploadProgress, false) // 
        // header
        if (typeof headers === 'object' && headers) {
          Object.keys(headers).forEach((k) => {
            client.setRequestHeader(k, headers[k])
          })
        }
        client.send(fmData)
      }).then(
        // 
        function (resData) {
          if (that.value) {
            that.loading = 2
            that.$emit('crop-upload-success', resData, field, ki)
          }
        },
        // 
        function (sts) {
          if (that.value) {
            that.loading = 3
            that.hasError = true
            that.errorMsg = lang.fail
            that.$emit('crop-upload-fail', sts, field, ki)
          }
        }
      )
    }
  },
  created () {
    // esc
    document.addEventListener('keyup', (e) => {
      if (this.value && (e.key === 'Escape' || e.keyCode === 27)) {
        this.off()
      }
    })
  }
}
</script>

<!--
<style lang='sass' src="./scss/upload.scss">
</style> -->

<style>
  /*@charset "UTF-8";*/
  @-webkit-keyframes vicp_progress {
    0% {
      background-position-y: 0;
    }
    100% {
      background-position-y: 40px;
    }
  }

  @keyframes vicp_progress {
    0% {
      background-position-y: 0;
    }
    100% {
      background-position-y: 40px;
    }
  }

  @-webkit-keyframes vicp {
    0% {
      opacity: 0;
      -webkit-transform: scale(0) translatey(-60px);
      transform: scale(0) translatey(-60px);
    }
    100% {
      opacity: 1;
      -webkit-transform: scale(1) translatey(0);
      transform: scale(1) translatey(0);
    }
  }

  @keyframes vicp {
    0% {
      opacity: 0;
      -webkit-transform: scale(0) translatey(-60px);
      transform: scale(0) translatey(-60px);
    }
    100% {
      opacity: 1;
      -webkit-transform: scale(1) translatey(0);
      transform: scale(1) translatey(0);
    }
  }

  .vue-image-crop-upload {
    position: fixed;
    display: block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    z-index: 10000;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.65);
    -webkit-tap-highlight-color: transparent;
    -moz-tap-highlight-color: transparent;
  }

  .vue-image-crop-upload .vicp-wrap {
    -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.23);
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.23);
    position: fixed;
    display: block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    z-index: 10000;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 600px;
    height: 330px;
    padding: 25px;
